import { useState, useRef, useEffect } from "react";
import { useSelector } from "react-redux";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Collapse from "react-bootstrap/Collapse";
import { Scrollama, Step } from "react-scrollama";
import { MapContainer, TileLayer, GeoJSON } from "react-leaflet";
import { useMediaQuery } from "react-responsive";
import L from "leaflet";
import ChangeView from "./ChangeView.jsx";
import MarkerGeo from "./MarkerGeo.jsx";
import germany from "../../data/germany.json";
import nrw from "../../data/nrw.json";
import muensterland from "../../data/muensterland.json";
import muenster from "../../data/muenster.json";
import nothing from "../../data/nothing.json";
import geoOneJSON from "../../data/geo1.json";

import lichthofGeo from "../../img/geo_1_lichthof.jpg";
import geoDronePhoto from "../../img/GEO1_jan_lehmann.jpg";

delete L.Icon.Default.prototype._getIconUrl;
L.Icon.Default.mergeOptions({
  shadowUrl:
    "https://cdnjs.cloudflare.com/ajax/libs/leaflet/1.0.0/images/marker-shadow.png",
});

//centers for the map at all scrollama steps
const germanyCenter = [51.8, 3.4];
const nrwCenter = [51.5, 5.2];
const muensterCenter = [52.05, 6.3];
const stadtCenter = [51.9583, 7.4];
const geoCenter = [51.969371, 7.592296];

//mobile centers
const germanyCenterMobile = [51.746079, 10.601846];
const nrwCenterMobile = [51.960667, 7.626135];
const muensterCenterMobile = [51.961869, 7.383207];
const stadtCenterMobile = [51.960667, 7.626135];
const geoCenterMobile = [51.969371, 7.595696];

function Intro() {
  const geoOne = geoOneJSON.features[0];
  const language = useSelector((state) => state.language);
  const isTabletOrMobile = useMediaQuery({ query: "(max-width: 767px)" });
  const [currentStepIndex, setCurrentStepIndex] = useState(null);
  const [currentCenter, setCurrentCenter] = useState(
    isTabletOrMobile ? germanyCenterMobile : germanyCenter
  );
  const [currentZoom, setCurrentZoom] = useState(3);
  const [geoData, setGeoData] = useState(germany);
  const geoJsonLayer = useRef(null);

  useEffect(() => {
    if (geoJsonLayer.current) {
      geoJsonLayer.current.clearLayers().addData(geoData);
    }
  }, [geoData]);

  // This callback fires when a Step hits the offset threshold. It receives the
  // data prop of the step, which in this demo stores the index of the step.
  const onStepEnter = ({ data }) => {
    setCurrentStepIndex(data);
    if (data === 0 || data === 1) {
      setCurrentCenter(isTabletOrMobile ? germanyCenterMobile : germanyCenter);
      setCurrentZoom(isTabletOrMobile ? 5 : 5.9);
      setGeoData(germany);
    }
    if (data === 2) {
      setCurrentCenter(isTabletOrMobile ? nrwCenterMobile : nrwCenter);
      setCurrentZoom(isTabletOrMobile ? 7 : 7.5);
      setGeoData(nrw);
    }
    if (data === 3) {
      setCurrentCenter(
        isTabletOrMobile ? muensterCenterMobile : muensterCenter
      );
      setCurrentZoom(isTabletOrMobile ? 8 : 8.7);
      setGeoData(muensterland);
    }
    if (data === 4) {
      setCurrentCenter(isTabletOrMobile ? stadtCenterMobile : stadtCenter);
      setCurrentZoom(isTabletOrMobile ? 10.5 : 10.9);
      setGeoData(muenster);
    }
    if (data === 5) {
      setCurrentCenter(isTabletOrMobile ? geoCenterMobile : geoCenter);
      setCurrentZoom(isTabletOrMobile ? 17 : 17);
      setGeoData(nothing);
    }
    return null;
  };

  const style = { fillColor: " #4C4C4A", color: "#4C4C4A", fillOpacity: 0.6 };

  return (
    <>
      <div id='Intro'>
        <div id='storymap-container'>
          <MapContainer
            scrollWheelZoom={false}
            zoomControl={false}
            dragging={false}
            tap={false}
            center={germanyCenter}
            zoom={5}
            id='storymap'>
            <ChangeView center={currentCenter} zoom={currentZoom} />
            <TileLayer
              attribution='&copy; <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
              url='http://{s}.tile.osm.org/{z}/{x}/{y}.png'
            />
            <GeoJSON
              ref={geoJsonLayer}
              key={"theOneGEOJSON"}
              data={geoData}
              //onEachFeature={onEachPolygon}
              style={style}
            />
            <MarkerGeo point={geoOne} key={geoOne.id} />
          </MapContainer>
        </div>
        <Scrollama onStepEnter={onStepEnter}>
          <Step data={1} key={1}>
            <div
              className={
                isTabletOrMobile
                  ? "storymap-description mobile first overflow-x"
                  : "storymap-description first"
              }>
              {language === "german" ? (
                <>
                  {" "}
                  <h2>Willkommen in deinem neuen Zuhause!</h2>
                  <p>
                    Schn, dass du dich entschieden hast, nach Mnster zu kommen
                    und umso schner, dass du ab sofort Geoinformatik studieren
                    wirst!
                  </p>
                  <p>
                    Damit du einen guten Start in dein Studierendenleben hast,
                    wollen wir dir auf dieser Seite hilfreiche Tipps geben und
                    dir die schnsten Orte in Mnster zeigen. Ganz auf die
                    Geoinformatiker-Art haben wir dafr diese Website mit
                    interaktiven Karten versehen. Diese erste Karte, die du im
                    Hintergrund sehe kannst, wird dich Schritt fr Schritt nach
                    Mnster bringen und damit du nie die Orientierung verlierst,
                    ist das GEO-Gebude die ganze Zeit mit dem ifgi-Logo
                    markiert.
                  </p>
                  <img
                    src={lichthofGeo}
                    width='100%'
                    height='auto'
                    alt={
                      language === "german"
                        ? "Lichthof des GEO-Gebudes"
                        : "Hall of the GEO-building"
                    }
                  />
                  <p>
                    Mnster liegt weit im Westen Deutschlands, nahe der
                    Niederlndischen Grenze, wie du auf der Karte sehen kannst.
                    Scrolle jetzt nach unten, um fortzufahren.
                  </p>
                </>
              ) : language === "englisch" ? (
                <>
                  {" "}
                  <h2>Welcome to Your New Home!</h2>
                  <p>
                    Welcome to Mnster and also welcome to the Institute of
                    Geoinformatics.
                  </p>
                  <p>
                    To help you starting your life in Mnster, we want to give
                    you some tips and basic information and show you the most
                    important places in Mnster. In true geoinformatics fashion,
                    we provide you this website with interactive maps. On this
                    first map which you can see in the background, you will be
                    guided step by step to the city of Mnster. The ifgi-logo
                    marks the GEO-building to not get disorientated on the map.
                  </p>
                  <img
                    src={lichthofGeo}
                    width='100%'
                    height='auto'
                    alt={
                      language === "german"
                        ? "Lichthof des GEO-Gebudes"
                        : "Hall of the GEO-building"
                    }
                  />
                  <p>
                    Mnster is far in the west of Germany near the border to the
                    Netherlands as you can see on the map. Now, scroll down to
                    go on.
                  </p>
                </>
              ) : (
                <></>
              )}
            </div>
          </Step>
          <Step data={2} key={2}>
            <div
              className={
                isTabletOrMobile
                  ? "storymap-description mobile overflow-x"
                  : "storymap-description"
              }>
              {language === "german" ? (
                <>
                  <h2>Ein Teil Nordrhein-Westfalens</h2>
                  <p>
                    Nordrhein-Westfalen (NRW) ist das bevlkerungsreichste
                    Bundesland Deutschlands. Und mit ber 300.000 Einwohnerinnen
                    und Einwohnern ist Mnster unter den Top-Ten Stdten NRWs.
                  </p>
                </>
              ) : language === "englisch" ? (
                <>
                  <h2>Part of North Rhine-Westphalia</h2>
                  <p>
                    North Rhine-Westphalia (NRW) is the most populated federal
                    state of Germany. With over 300,000 inhabitants Mnster is
                    on of the top ten citys in NRW.
                  </p>
                </>
              ) : (
                <></>
              )}
              <a
                data-flickr-embed='true'
                data-header='true'
                href='https://www.flickr.com/photos/132810489@N02/41935417150/in/photolist-26TFYvC-fARTxu-fABBdR-fABR96-fAS98u-68TqNH-fABQnP-fABKP4-fABCX6-fAS6Th-fAS7iC-fABRVR-fAS341-fABD3i-fABQHt-fABDiR-2jqPVHw-MmAkxj-fABB3z-fARVoy-fARTmf-fABCaH-RvUoFZ-fAS3V5-fARUh9-fARTM3-fAS8wj-2jYQSxF-6FLeSk-fAS65u-2jnkCgD-fAS3oN-fAS9gm-fARZuu-fARUrq-fABMLv-fABSo2-23ZHyN1-2fdQ8QC-fABKUt-fAS2yf-fAS2qC-fABKEV-fABCPe-fAS7VU-fAS2hE-fAS7Aj-fABCFZ-fAS3vC-fAS8Ho'
                title='Kln'>
                <img
                  src='https://live.staticflickr.com/941/41935417150_cffb157d6c_c.jpg'
                  width='100%'
                  height='auto'
                  alt='Kln'
                />
              </a>
              {language === "german" ? (
                <>
                  <p>
                    Als Teil deines Studienbeitrags zahlst du jedes Semester ein
                    Semesterticket, mit dem du kostenlos den ffentlichen
                    Personennahverkehr in ganz NRW nutzen kannst. Du kannst also
                    ohne weitere Kosten von Bonn nach Kln, von Mnster nach
                    Dsseldorf, von Dortmund nach Aachen oder vom Domplatz in
                    Mnster zum Hauptbahnhof fahren, um nur einige Mglichkeiten
                    zu nennen. Dein Ticket reicht sogar etwas ber die Grenzen
                    NRWs, das auf der Karte eingefrbt ist, hinaus.
                  </p>
                </>
              ) : language === "englisch" ? (
                <>
                  <p>
                    As part of your tuition fee, you pay for a semester ticket
                    every semester, which allows you to use public transport
                    throughout NRW at no additional cost. This means that you
                    can travel from Bonn to Cologne, from Mnster to Dsseldorf,
                    from Dortmund to Aachen or from Domplatz in Mnster to the
                    main train station, to name just a few possibilities.
                    Partly, the ticket can bring you further then the borders of
                    NRW, which is displayed on the map.
                  </p>
                </>
              ) : (
                <></>
              )}
            </div>
          </Step>
          <Step data={3} key={3}>
            <div
              className={
                isTabletOrMobile
                  ? "storymap-description mobile overflow-x"
                  : "storymap-description"
              }>
              {language === "german" ? (
                <>
                  <h2>Mnsterland: Felder, Fahrrder und Freiheit.</h2>
                  <p>
                    Mit 188,7 Metern Hhe ist der Westerberg der hchste Punkt
                    des Mnsterlandes und genau aus diesem Grund ist das Fahrrad
                    der absolute Renner in Mnster und Umgebung. Jede Strecke
                    lsst sich ohne groe Anstrengung bewltigen, macht Spa und
                    schont Geldbeutel und Umwelt. Auerdem kann sich das
                    Mnsterland sehen lassen. Eine Vielzahl von Burgen und
                    Schlssern, sowie viel Natur laden zu entspannten Ausflgen
                    ein. Um mehr ber das Mnsterland zu erfahren, besuche gerne
                    die{" "}
                    <a
                      href='https://www.muensterland.com/'
                      target='_blank'
                      rel='noreferrer'>
                      offizielle Homepage
                    </a>
                    .
                  </p>
                </>
              ) : language === "englisch" ? (
                <>
                  <h2>Mnsterland. Fields, Bicycles and Beauty.</h2>
                  <p>
                    The Westerberg is the highest point of the Mnsterland with
                    188.7 meters. Therefore, it is always the right decision to
                    ride your bicycle. It is easy to use because of the flat
                    terrain, it is cheap and environmentally friendly and beside
                    that it makes a lot of fun. With a huge number of castles
                    and palaces and a lot of nature the Mnsterland is always
                    worth a tour. To learn more about the Mnsterland, check out
                    the{" "}
                    <a
                      href='https://www.muensterland.com/en/'
                      target='_blank'
                      rel='noreferrer'>
                      official homepage
                    </a>
                    .
                  </p>
                </>
              ) : (
                <></>
              )}

              <a
                data-flickr-embed='true'
                data-header='true'
                href='https://www.flickr.com/photos/nstening/31218741111/in/photolist-PyGd66-2cdrW6M-no8QTm-yKg4HR-5Wfvt7-nZs8RW-p7RNu-Mkjdp1-2jfZcDi-2jibth3-2kDmh36-2h9qr1W-2hCKbZE-LTYMcL-2jaU9NG-MNLT9J-Kbo3MT-2j2fhsu-29fnmWj-MsZUDn-2ma6Fw8-2m9cX4W-2bq3Y9U-2kQ3rjf-2eWFnGZ-2j8ijRX-2jg38TK-4JEg4M-2kWRaDa-LYnGvH-2gW5Vs6-wM7pbm-f66WwC-23TE3he-QP5WBZ-XmXhak-NLVYbT-NrKk6N-HLHSeq-2iXT5JR-2e9VquU-Byqs6Y-2ki4Q35-Pm6AXC-NsGjyf-G4dJz7-2hffvW7-HukrjX-Qf4KMF-2g1ZFwP'
                title='Schloss Nordkirchen on the 1. Advent close to Mnster Germany'>
                <img
                  src='https://live.staticflickr.com/5716/31218741111_dd517d7bc7_c.jpg'
                  width='100%'
                  height='auto'
                  alt='Schloss Nordkirchen on the 1. Advent close to Mnster Germany'
                />
              </a>

              {language === "german" ? (
                <>
                  <p>
                    Neben Mnster sind auch die Kreise Borken, Coesfeld,
                    Steinfurt und Warendorf auf unserer Karte zu erkennen, die
                    im Gesamten als das Mnsterland bezeichnet werden.
                  </p>
                </>
              ) : language === "englisch" ? (
                <>
                  <p>
                    In addition to Mnster, the districts of Borken, Coesfeld,
                    Steinfurt and Warendorf can also be seen on our map, which
                    are collectively called Mnsterland.
                  </p>
                </>
              ) : (
                <></>
              )}
            </div>
          </Step>
          <Step data={4} key={4}>
            <div
              className={
                isTabletOrMobile
                  ? "storymap-description mobile overflow-x"
                  : "storymap-description"
              }>
              {language === "german" ? (
                <>
                  <h2>Eine Hansestadt mit bewegter Vergangenheit</h2>
                  <p>
                    Mnster ist eine Stadt mit viel Historie und entsprechend
                    viel gibt es hier zu erleben. Zwischen einer Vielzahl von
                    Sehenswrdigkeiten und kulturellen Einrichtungen bleibt
                    Platz fr eine abwechslungsreiche Freizeit, denn Mnster ist
                    lebendig und hat immer etwas zu bieten. Um einen kleinen
                    berblick zu bekommen, haben wir ganz am Ende dieser Seite
                    eine interaktive Karte mit den schnsten Orten in Mnster
                    fr dich vorbereitet. Sei also gespannt.
                  </p>
                </>
              ) : language === "englisch" ? (
                <>
                  <h2>Hanseatic City With an Eventful History</h2>
                  <p>
                    Mnster is a city with a lot of history and correspondingly
                    there is much to experience here. Between a multitude of
                    sights and cultural institutions, there is also plenty of
                    room for a varied leisure time, because Mnster is a lively
                    city with much to offer. To give you a brief overview, we
                    have an interactive map at the very end of this page with
                    the most beautiful places in Mnster. So stay curious.
                  </p>
                </>
              ) : (
                <></>
              )}
              <a
                data-flickr-embed='true'
                data-header='true'
                href='https://www.flickr.com/photos/155090883@N08/32597005718/in/photolist-REub8Y-2iDzH91-J6ZmqL-2m2vYyY-CNgHHE-b3D8L6-CNoVY6-DHtChE-DBwpyt-CNh5km-DHtty7-DBwDNk-CNp7Z4-DHsVzC-DchEvT-DKMptK-DiEDwE-DKMuHc-DHtEmj-DBw4XK-DBwJK4-DBvVht-DznAb1-CNgvxN-DKMGo6-DiEyef-DKMawM-CNhcij-DKMY5X-DiENk1-CNgBh3-CNgV5s-DKMCpz-DBwEGV-DciCRt-DiF4vS-DBvPbv-CNgVXE-DHtpzA-DBwhGc-Dzo6J9-DiFa5N-DKMQ2x-CNh7EG-pHteJg-24xd2P5-Hb3nXx-RTZHFt-2kkAfYx-2k8cQYy'
                title='Mnster'>
                <img
                  src='https://live.staticflickr.com/4907/32597005718_761ed5a845_c.jpg'
                  width='100%'
                  height='auto'
                  alt='Mnster'
                />
              </a>
              {language === "german" ? (
                <>
                  <p>
                    Auf der Karte kannst du nun die einzelnen Stadtteile
                    Mnsters erkennen. Das Geo befindet sich, wie du siehst,
                    sehr nahe der Altstadt und somit in bester Lage.
                  </p>
                </>
              ) : language === "englisch" ? (
                <>
                  <p>
                    On the map you can see the outlines of the city of Mnster
                    and its individual districts. The GEO is quite centrally
                    located and near the historic city center.
                  </p>
                </>
              ) : (
                <></>
              )}
            </div>
          </Step>
          <Step data={5} key={5}>
            <div
              className={
                isTabletOrMobile
                  ? "storymap-description mobile overflow-x"
                  : "storymap-description"
              }>
              {language === "german" ? (
                <>
                  <h2>Dein Institut</h2>
                  <p>
                    Das Institut fr Geoinformatik (ifgi) befindet sich im
                    nordwestlichen Teil von Mnster auf dem
                    naturwissenschaftlichen Campus. Hier sitzen wir  zusammen
                    mit den Instituten fr Landschaftskologie, Geographie und
                    Didaktik der Geographie  im schnen GEO1-Gebude. Die
                    Rume des ifgi befinden sich hauptschlich in den ersten
                    beiden Obergeschossen. Einige eurer Veranstaltungen werden
                    auch im GEO1 stattfinden, z.B. im groen GEO1-Hrsaal.
                  </p>
                </>
              ) : language === "englisch" ? (
                <>
                  <h2>Your Institute</h2>
                  <p>
                    The Institute for Geoinformatics (ifgi) is located in the
                    northwestern part of Muenster on the campus of natural
                    sciences. Here we reside in the beautiful GEO1 building
                    together with the institutes of Landscape Ecology, Geography
                    and Didactics of Geography. The rooms of ifgi are mainly
                    located on the 1st and 2nd floor. Some of your courses and
                    lectures will also take place in GEO1, e.g., in the GEO1
                    lecture hall.
                  </p>
                </>
              ) : (
                <></>
              )}
              <img
                src={geoDronePhoto}
                width='100%'
                height='auto'
                title={
                  language === "german"
                    ? "Foto von Jan Lehmann"
                    : "photo by Jan Lehmann"
                }
                alt={
                  language === "german"
                    ? "GEO-Gebudes, Foto von Jan Lehmann"
                    : "GEO-building, photo by Jan Lehmann"
                }
              />
            </div>
          </Step>
          <Step data={6} key={6}>
            <div
              style={{
                marginTop: "100vh",
                marginLeft: "5vw",
                marginRight: "5vw",
                border: "1px solid gray",
                backgroundColor: "white",
                opacity: "0.0",
                padding: "30px",
              }}>
              End of storymap
            </div>
          </Step>
        </Scrollama>
      </div>
    </>
  );
}

export default Intro;
